const { protocol, origin } = window.location;
var host;
if (protocol === "http:") {
  host = `http://localhost:3005`;
} else {
  host = origin;
}
const versions = {
  first_version: `/v1/api`,
};
const models = {
  urls: "/u",
  tags: "/t",
};
var urls = {
  create_url: "/post",
  get_urls: "/get",
  update_url: "/put",
  delete_url: "/delete",
  delete_all_urls: "/delete_all",
};

var tags = {
  create_tag: "/post",
  get_tags: "/get",
  update_tag: "/put",
  delete_tag: "/delete",
  delete_all_tags: "/delete_all",
};

//  all apis are ready for urls
Object.keys(urls).map((key) => {
  urls[key] = host + versions.first_version + models.urls + urls[key];
});
//  all apis are ready for tags
Object.keys(tags).map((key) => {
  tags[key] = host + versions.first_version + models.tags + tags[key];
});

const apis = {
  urls,
  tags,
};
console.log("apis", apis, tags);
export default apis;
