import axios from "axios"
import apis from "../api_urls"

export const getURLS = async body => {
  const { urls } = apis
  const { setUpdateURLS } = body
  console.log("body", body)
  await axios
    .get(urls.get_urls)
    .then(res => {
      // log the response
      console.log("get req res: ", res)
      //  update the urls array
      res.data.forEach((element, index) => {
        element["key"] = index
      })
      console.log("res.data", res.data)
      setUpdateURLS(res.data)
    })
    .catch(err => {
      console.log("get req err: ", err)
    })
}

export const createURL = async body => {
  const { urls } = apis
  const { values, afterFinish } = body
  await axios
    .post(urls.create_url, values)
    .then(res => {
      //  log the response data
      console.log("post req res: ", res)
      //  udpate the state
      afterFinish(res.data)
    })
    .catch(err => {
      console.log("post req err: ", err)
    })
}

export const updateURL = async body => {
  const { urls } = apis
  const { newURL, afterUpdateURL } = body
  const { _id } = newURL
  await axios
    .put(urls.update_url + "/" + _id, newURL)
    .then(res => {
      //  log the response data
      console.log("put req res: ", res)
      const { url } = res.data
      //  udpate the state
      afterUpdateURL(url)
    })
    .catch(err => {
      console.log("put req err: ", err)
    })
}

export const deleteURL = async body => {
  const { urls } = apis
  const { _id, afterDelURL } = body
  await axios
    .delete(urls.delete_url + `/${_id}`)
    .then(res => {
      //  log the response data
      console.log("delete req res: ", res)
      const { id } = res.data
      //  udpate the state
      if (id) {
        afterDelURL(id)
      }
    })
    .catch(err => {
      console.log("delete req err: ", err)
    })
}

export const deleteAllURL = async body => {
  const { selectedIDS, afterDelSelecteURLS } = body
  const { urls } = apis

  const fetchUrl = async eachSelectedId =>
    await axios.delete(urls.delete_url + "/" + eachSelectedId._id)
  const promises = selectedIDS.map(fetchUrl)

  let responses = await Promise.all(promises)
  var deletedIds = []
  responses.forEach(res => {
    //    let msg = `${resp.config.url} -> ${resp.headers.server}: ${resp.status}`
    const { id: _id } = res.data
    deletedIds.push(_id)
  })
  console.log("deleted", deletedIds)
  afterDelSelecteURLS(deletedIds)
}
