import React, { Component } from "react";
import TabComponent from "./TabComponent/tabComponent";
import { getURLS } from "../apiFolder/urls";
import { getTAGS } from "../apiFolder/tags";
import { Layout } from "antd";

import urlsData from "./urls.json";
import tagsData from "./tags.json";
import "./styles.css";

const { Header, Content, Sider, Footer } = Layout;

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      urls: [] || urlsData.urls,
      tags: [] || tagsData.tags,
    };
  }

  initialize() {
    const body_1 = {
      setUpdateURLS: this.setUpdateURLS,
    };
    getURLS(body_1);

    const body_2 = {
      setUpdateTAGS: this.setUpdateTAGS,
    };
    getTAGS(body_2);
  }
  componentDidMount() {
    this.initialize();
  }
  setUpdateURLS = (newArray) => {
    this.setState({ urls: newArray });
  };
  setUpdateTAGS = (newArray) => {
    this.setState({ tags: newArray });
  };

  render() {
    const { urls, tags } = this.state;
    return (
      <>
        <div>
          <Layout>
            <Header
              className="header"
              style={{
                position: "fixed",
                zIndex: 1,
                width: "100%",
              }}
            >
              <div
                className="logo"
                style={{ color: "white", fontSize: "20px" }}
              >
                MY IMPORTANT URLS
              </div>
            </Header>
            <Layout style={{ marginTop: "40px" }}>
              <Layout
                style={{
                  padding: "0 24px 24px",
                }}
              >
                <Content
                  style={{
                    padding: 24,
                    margin: 0,
                    minHeight: 200,
                  }}
                >
                  <TabComponent
                    data={{ urls: urls, tags: tags }}
                    methods={{
                      setUpdateUrls: this.setUpdateURLS,
                      setUpdateTags: this.setUpdateTAGS,
                    }}
                  />
                </Content>
              </Layout>
            </Layout>
          </Layout>
        </div>
      </>
    );
  }
}
export default Main;
