import axios from "axios"
import apis from "../api_urls"

export const getTAGS = async body => {
  const { tags } = apis
  const { setUpdateTAGS } = body
  await axios
    .get(tags.get_tags)
    .then(res => {
      // log the response
      console.log("before tags data : ", res)
      //  update the urls array
      res.data.forEach((element, index) => {
        element["key"] = index
      })
      console.log("after tags data", res.data)
      setUpdateTAGS(res.data)
    })
    .catch(err => {
      console.log("tags data err: ", err)
    })
}

export const createTAG = async body => {
  const { tags } = apis
  const { values, afterFinish } = body
  console.log(body)
  await axios
    .post(tags.create_tag, values)
    .then(res => {
      //  log the response data
      console.log("create tag req res : ", res)
      //  udpate the state
      afterFinish(res.data)
    })
    .catch(err => {
      console.log("create tag req err: ", err)
    })
}

export const updateTAG = async body => {
  const { tags } = apis
  const { newTAG, afterUpdateTAG } = body
  const { _id } = newTAG
  await axios
    .put(tags.update_tag + "/" + _id, newTAG)
    .then(res => {
      //  log the response data
      console.log("update tag req res: ", res)
      const { url } = res.data
      //  udpate the state
      afterUpdateTAG(url)
    })
    .catch(err => {
      console.log("update tag req err: ", err)
    })
}

export const deleteTAG = async body => {
  const { tags } = apis
  const { _id, afterDelTAG } = body
  await axios
    .delete(tags.delete_tag + `/${_id}`)
    .then(res => {
      //  log the response data
      console.log("delete tag req res: ", res)
      const { id } = res.data
      //  udpate the state
      if (id) {
        afterDelTAG(id)
      }
    })
    .catch(err => {
      console.log("delete tag req err: ", err)
    })
}

export const deleteAllTAG = async body => {
  const { selectedIDS, afterDelSelectedTAGS } = body
  const { tags } = apis

  const fetchUrl = async eachSelectedId =>
    await axios.delete(tags.delete_tag + "/" + eachSelectedId._id)
  const promises = selectedIDS.map(fetchUrl)

  let responses = await Promise.all(promises)
  var deletedIds = []
  responses.forEach(res => {
    const { id: _id } = res.data
    deletedIds.push(_id)
  })
  console.log("deleted", deletedIds)
  afterDelSelectedTAGS(deletedIds)
}
