import React, { useState } from "react";
import { Table, Space, Button, Typography, Tooltip, Select, Tag } from "antd";
import { Modal, Form, Input, Checkbox, notification } from "antd";
import {
  createTAG,
  updateTAG as updateTAGAPI,
  deleteTAG,
  deleteAllTAG,
} from "../../../apiFolder/tags";
const { Paragraph } = Typography;
const { Option } = Select;

const TAGS = (props) => {
  const [form] = Form.useForm();
  const { urls, tags, setUpdateTags } = props;
  const [title, setTitle] = useState("");

  const [selectedTags, setSelectedTags] = useState([]);
  const rowSelection = {
    selectedTags,
    onChange: setSelectedTags,
  };
  const openTAGUrl = (url) => {
    window.open(url, "_blank");
  };

  const afterUpdateTAG = (newTAG) => {
    const index = tags.findIndex((obj) => obj._id === newTAG._id);
    const duplicateDataSource = [...tags];
    duplicateDataSource[index] = newTAG;
    setUpdateTags(duplicateDataSource);
  };
  const updateTAG = (newTAG) => {
    const body = {
      newTAG,
      afterUpdateTAG,
    };
    updateTAGAPI(body);
  };

  const afterDelTAG = (_id) => {
    setUpdateTags(tags.filter((obj) => obj._id !== _id));
    setSelectedTags([]);
  };

  const delTAG = (_id) => {
    const body = {
      _id,
      afterDelTAG,
    };
    deleteTAG(body);
  };

  const afterDelSelectedTAGS = (deletedIds) => {
    setUpdateTags(tags.filter((obj) => !deletedIds.includes(obj._id)));
    setSelectedTags([]);
  };
  const convertSelectedIndexToIDS = () => {
    const sel_tags = [];
    selectedTags.map((index) => {
      const { _id } = tags[index];
      sel_tags.push({ _id });
    });
    return sel_tags;
  };

  const delSelectedTAGS = () => {
    const selectedIDS = convertSelectedIndexToIDS();
    console.log(selectedIDS);
    const body = {
      selectedIDS,
      afterDelSelectedTAGS,
    };
    console.log(body);
    deleteAllTAG(body);
  };

  const columns = [
    {
      title: "NO.",
      dataIndex: "no",
      key: "no",
      render: (text, record) => {
        return record.key + 1;
      },
    },
    {
      title: "TITLE",
      dataIndex: "title",
      key: "title",
      render: (text, record) => {
        return (
          <Paragraph
            style={{ width: 200 }}
            ellipsis
            editable={{
              tooltip: "click to edit Tag Name",
              onChange: (title) =>
                updateTAG({
                  ...record,
                  title,
                }),
            }}
          >
            {text}
          </Paragraph>
        );
      },
    },
    {
      title: "URLS",
      dataIndex: "urls",
      key: "urls",
      render: (text, record) => {
        return (
          <Select
            maxTagCount={3}
            mode="multiple"
            allowClear
            style={{ width: 400 }}
            placeholder="Please select Urls"
            defaultValue={record.urls}
            onChange={(urls) =>
              updateTAG({
                ...record,
                urls,
              })
            }
          >
            {urls.map((obj) => {
              return (
                <Option key={obj.key} value={obj.url}>
                  {obj.title}
                </Option>
              );
            })}
          </Select>
        );
      },
    },
    {
      title: "ACTION",
      dataIndex: "action",
      key: "action",
      render: (text, record) => {
        return (
          <Space>
            <Tooltip title={"Open in New Tab"}>
              <Button
                type="primary"
                ghost
                onClick={() => {
                  record.urls.forEach((eachUrl) => {
                    openTAGUrl(eachUrl);
                  });
                }}
              >
                OPEN
              </Button>
            </Tooltip>
            <Button type="primary" danger onClick={() => delTAG(record._id)}>
              DELETE
            </Button>
          </Space>
        );
      },
    },
  ];

  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    onReset();
  };

  const afterFinish = (values) => {
    console.log("Success:", values);
    values = { ...values, key: tags.length };
    const newTagsArray = [...tags];
    newTagsArray.push(values);

    setUpdateTags(newTagsArray);
    handleOk();
    onSuccessNotification();
    onReset();
  };
  const onFinish = (values) => {
    const body = {
      values,
      afterFinish,
    };
    createTAG(body);
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  const onReset = () => {
    form.resetFields();
  };
  const onSuccessNotification = () => {
    notification.success({
      message: "Notification Title",
      description: "Tag is successfully created",
    });
  };
  const onSearch = ({ target: { value } }) => setTitle(value);

  return (
    <div>
      <Table
        dataSource={tags.filter((eachObject) =>
          eachObject.title.toLowerCase().includes(title.toLowerCase())
        )}
        columns={columns}
        bordered
        scroll={{ y: 281 }}
        rowSelection={rowSelection}
        title={() => (
          <Space style={{ marginLeft: "70%" }}>
            <Input
              placeholder="Enter search title!"
              onChange={onSearch}
              enterButton
              allowClear
            />

            <Tooltip title={"Add New Tag"}>
              <Button type="primary" onClick={showModal}>
                ADD
              </Button>
            </Tooltip>
            <Tooltip title={"Delete selected tags"}>
              <Button
                disabled={!selectedTags.length}
                type="primary"
                danger
                onClick={delSelectedTAGS}
              >
                DELETE
              </Button>
            </Tooltip>
          </Space>
        )}
      />
      <Modal
        title="Create New Tag"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        footer={false}
        scroll={{ y: 300 }}
      >
        <Form
          form={form}
          name="create_new_tag"
          labelCol={{
            span: 8,
          }}
          wrapperCol={{
            span: 16,
          }}
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          autoComplete="off"
        >
          <Form.Item
            label="TITLE"
            name="title"
            rules={[
              {
                required: true,
                message: "Please input your Title!",
              },
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            label="URLS"
            name="urls"
            rules={[
              {
                required: true,
                message: "Please input your URLS!",
              },
            ]}
          >
            <Select
              mode="multiple"
              allowClear
              placeholder="Please select Urls"
              // defaultValue={urls}
              onChange={(e) => {
                console.log("e", e);
                // updateTAGURLS(record.key, e)
              }}
            >
              {urls.map((obj) => {
                return (
                  <Option key={obj.key} value={obj.url}>
                    {obj.title}
                  </Option>
                );
              })}
            </Select>
          </Form.Item>

          <Form.Item
            wrapperCol={{
              offset: 8,
              span: 16,
            }}
          >
            <Space>
              <Button type="primary" htmlType="submit">
                Create
              </Button>
              <Button type="primary" danger onClick={onReset}>
                Reset
              </Button>
            </Space>
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
};

export default TAGS;
