import React from "react";
import { Tabs } from "antd";

import URLComponent from "./URLComponent/urls";
import TAGSComponent from "./TagsComponent/tags";

const { TabPane } = Tabs;
const tabComponent = (props) => {
  const { urls, tags } = props.data;
  const { setUpdateUrls, setUpdateTags } = props.methods;
  const tabHeading = (heading) => {
    return (
      <span style={{ color: "black", fontWeight: "bold", fontSize: 20 }}>
        {heading}
      </span>
    );
  };
  return (
    <Tabs defaultActiveKey="1" centered size="large">
      <TabPane tab={tabHeading("URLS")} key="1">
        <URLComponent urls={urls} setUpdateUrls={setUpdateUrls} />
      </TabPane>
      <TabPane tab={tabHeading("TAGS")} key="2">
        <TAGSComponent tags={tags} urls={urls} setUpdateTags={setUpdateTags} />
      </TabPane>
    </Tabs>
  );
};

export default tabComponent;
