import React, { useEffect, useState } from "react";
import { Table, Space, Button, Typography, Tooltip, Modal } from "antd";
import { Form, Input, notification } from "antd";
import {
  createURL,
  deleteURL,
  deleteAllURL,
  updateURL as updateURLAPI,
} from "../../../apiFolder/urls/index";

const { Search } = Input;
const { Paragraph } = Typography;
const URLS = (props) => {
  const { urls, setUpdateUrls } = props;
  const [form] = Form.useForm();

  const [tooltipText, setTooltipText] = useState("Copy");
  const [selectedUrls, setSelectedUrls] = useState([]);
  const [title, setTitle] = useState("");
  const rowSelection = {
    selectedUrls,
    onChange: setSelectedUrls,
  };
  const openURL = (url) => {
    window.open(url, "_blank");
  };

  const afterUpdateURL = (newURL) => {
    const index = urls.findIndex((obj) => obj._id === newURL._id);
    const duplicateDataSource = [...urls];
    duplicateDataSource[index] = newURL;
    setUpdateUrls(duplicateDataSource);
  };
  const updateURL = (newURL) => {
    const body = {
      newURL,
      afterUpdateURL,
    };
    updateURLAPI(body);
  };
  const afterDelURL = (_id) => {
    setUpdateUrls(urls.filter((obj) => obj._id !== _id));
    setSelectedUrls([]);
  };
  const delURL = (_id) => {
    const body = {
      _id,
      afterDelURL,
    };
    deleteURL(body);
  };

  const columns = [
    {
      title: "NO.",
      dataIndex: "no",
      key: "no",
      render: (text, record) => {
        return record.key + 1;
      },
    },
    {
      title: "TITLE",
      dataIndex: "title",
      key: "title",
      render: (text, record) => {
        return (
          <Paragraph
            style={{ width: 200 }}
            ellipsis
            editable={{
              tooltip: "click to edit text",
              onChange: (title) =>
                updateURL({
                  ...record,
                  title,
                }),
            }}
          >
            {text}
          </Paragraph>
        );
      },
    },
    {
      title: "URL",
      dataIndex: "url",
      key: "url",
      render: (text, record) => {
        return (
          <Paragraph
            style={{ width: 200 }}
            ellipsis
            editable={{
              tooltip: "click to edit text",
              onChange: (url) => updateURL({ ...record, url }),
            }}
          >
            {text}
          </Paragraph>
        );
      },
    },
    {
      title: "ACTION",
      dataIndex: "action",
      key: "action",
      render: (text, record) => {
        return (
          <Space>
            <Tooltip title={tooltipText}>
              <Button
                type="primary"
                onClick={() => {
                  window.navigator.clipboard.writeText(record.url);
                  setTooltipText("Copied");
                  setTimeout(() => {
                    setTooltipText("Copy");
                  }, 800);
                }}
              >
                COPY
              </Button>
            </Tooltip>
            <Tooltip title={"Open in New Tab"}>
              <Button type="primary" ghost onClick={() => openURL(record.url)}>
                OPEN
              </Button>
            </Tooltip>
            <Button type="primary" danger onClick={() => delURL(record._id)}>
              DELETE
            </Button>
          </Space>
        );
      },
    },
  ];

  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    onReset();
  };

  const afterFinish = (values) => {
    console.log("Success:", values);
    values = { ...values, key: urls.length };
    const newUrlsArray = [...urls];
    newUrlsArray.push(values);
    setUpdateUrls(newUrlsArray);
    handleOk();
    onSuccessNotification();
    onReset();
  };
  const onFinish = (values) => {
    const body = {
      values,
      afterFinish,
    };
    createURL(body);
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  const onReset = () => {
    form.resetFields();
  };
  const onSuccessNotification = () => {
    notification.success({
      message: "Notification Title",
      description: "Url is successfully created",
    });
  };
  const convertSelectedIndexToIDS = () => {
    const sel_urls = [];
    selectedUrls.map((index) => {
      const { _id } = urls[index];
      sel_urls.push({ _id });
    });
    return sel_urls;
  };

  const afterDelSelecteURLS = (deletedIds) => {
    setUpdateUrls(urls.filter((obj) => !deletedIds.includes(obj._id)));
    setSelectedUrls([]);
  };
  const delSelectedURLS = () => {
    const selectedIDS = convertSelectedIndexToIDS();
    console.log(selectedIDS);
    const body = {
      selectedIDS,
      afterDelSelecteURLS,
    };
    deleteAllURL(body);
  };
  const onSearch = ({ target: { value } }) => setTitle(value);

  return (
    <div>
      <Table
        dataSource={urls.filter((eachObject) =>
          eachObject.title.toLowerCase().includes(title.toLowerCase())
        )}
        columns={columns}
        bordered
        rowSelection={rowSelection}
        scroll={{ y: 281 }}
        title={() => (
          <Space style={{ marginLeft: "70%" }}>
            <Tooltip title={"Enter Title"}>
              <Input
                placeholder="Enter search title!"
                onChange={onSearch}
                enterButton
                allowClear
              />
            </Tooltip>
            <Tooltip title={"Add New Url"}>
              <Button
                type="primary"
                onClick={() => {
                  showModal();
                }}
              >
                ADD
              </Button>
            </Tooltip>
            <Tooltip title={"Delete selected Url"}>
              <Button
                disabled={selectedUrls.length === 0}
                type="primary"
                danger
                onClick={delSelectedURLS}
              >
                DELETE
              </Button>
            </Tooltip>
          </Space>
        )}
      />
      <Modal
        title="Create New Url"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        footer={false}
      >
        <Form
          form={form}
          name="create_new_url"
          labelCol={{
            span: 8,
          }}
          wrapperCol={{
            span: 16,
          }}
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          autoComplete="off"
        >
          <Form.Item
            label="TITLE"
            name="title"
            rules={[
              {
                required: true,
                message: "Please input your Title!",
              },
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            label="URL"
            name="url"
            rules={[
              {
                required: true,
                message: "Please input your URL!",
              },
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            wrapperCol={{
              offset: 8,
              span: 16,
            }}
          >
            <Space>
              <Button type="primary" htmlType="submit">
                Create
              </Button>
              <Button type="primary" danger onClick={onReset}>
                Reset
              </Button>
            </Space>
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
};

export default URLS;
